@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onHighDesktop {
  @media (min-width: 1440px) {
    @content;
  }
}

@mixin onFullScreen {
  @media (min-width: 1980px) {
    @content;
  }
}
